import uniDrawer from '@/components/uni-drawer/uni-drawer.vue';
export default {
	components: {
		uniDrawer
	},
	data() {
		return {
			service: ['促销', '分期', '仅看有货'],
			serviceB: 0,
			classT: ['耳机', '户外', '配件'],
			classB: 0,
			showRigth: false, //筛选
			head: [{ txt: '综合', judge: 0 }, { txt: '销量', judge: 0 }, { txt: '价格', judge: 0 }],
			sortIndex: 0,
			datas: [
				{
					pinglun: '1348',
					"id": 28,
					"title": "小米空调",
					"cover": "https://tangzhe123-com.oss-cn-shenzhen.aliyuncs.com/public/5d8834543784d.jpg",
					"min_price": "25.00",
					"comments_count": 0,
					"comments_good_count": 0
				}
			]
		};
	},
	methods: {
		sort(e) {
			let _this = this;
			let index = 0;
			_this.sortIndex = e;
			for (let i = 0; i < _this.head.length; i++) {
				if (e == i) {//当前字体图标
					if (_this.head[i].judge == 0) {
						_this.head[i].judge = 1;//向上字体图标的颜色变色
					} else {
						_this.head[i].judge = 0;//向下字体图标的颜色变色
					}
				} else {//其他字体图标
					_this.head[i].judge = 1;
				}
			}

			// 获取数据
		},
		async showChange() {
			let [error, res] = await uni.request({
				url: 'http://ceshi3.dishait.cn/api/index_category/data'
			});
			console.log(res.data.data.category);
		},
		serviceD(e) {
			//点击当前筛选赋值样式
			this.serviceB = e;
		},
		classD(e) {
			this.classB = e;
		},
		Jump() {
			//跳转详情页面
			uni.navigateTo({
				url: '/components/home/xqing'
			});
		},
		res() {
			//关闭筛选
			let _this=this
			_this.serviceB=0
			_this.classB=0
			console.log('取消');
			// this.showRigth = false;
		},
		show(e) {
			//显示筛选
			this.showRigth = true;
		},
		hide() {
			//关闭筛选
			this.showRigth = false;
		},
		closeDrawer(e) {
			this.showRigth = false;
		},
		confirm() {}
	},
	created() {
		this.showChange();
	},
	onNavigationBarButtonTap(e) {
		if (e.index == 0) {
			// uni.navigateTo({
			// 	url: '/pages/shopping/index'
			// });
			console.log('购物');
		}
	}
};
